@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --light-primary-color: #0d9488;
  --light-secondary-color: white;
  --dark-primary-color: linear-gradient(#3F51B5, #9C27B0,#E91E63);
  --dark-secondary-color: #1e293b;
}

@font-face {
  font-family: "Onest";
  src: url("../../fonts/Onest-Regular.ttf") format("ttf");
  src: url("../../fonts/Onest-Bold.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "Onest", Fallback, sans-serif;
}